.CMS_Table_root {
  @apply z-[2];
}

.CMS_Table_table {
  color: var(--text-primary);

  @apply w-full
    text-sm
    text-left;
}

.CMS_Table_header {
  @apply text-xs;
}

.CMS_Table_header-row {
  @apply shadow-sm;
}

.CMS_Table_header-cell {
  @apply font-bold
    sticky
    top-0
    border-0
    p-0;
}

.CMS_Table_header-cell-content {
  color: var(--text-primary);
  background: var(--background-main);
  border-color: var(--background-light);

  @apply px-4
    py-3
    border-b
    text-[14px]
    truncate
    w-full;
}

.CMS_Table_body-row {
  background: var(--background-main);
  border-color: color-mix(in srgb, var(--background-divider) 25%, transparent);

  &:focus,
  &:hover {
    background: color-mix(in srgb, var(--primary-main) 15%, transparent);
    border-color: color-mix(in srgb, var(--primary-main) 15%, transparent);
  }

  @apply border-t
    focus:outline-none;
}

.CMS_Table_body-cell {
  color: var(--text-primary);

  &.CMS_Table_body-cell-has-link {
    @apply p-0;
  }

  &:not(.CMS_Table_body-cell-has-link) {
    @apply px-4
      py-3;
  }

  &.CMS_Table_body-cell-emphasis {
    color: var(--text-primary);

    @apply font-medium
      whitespace-nowrap;
  }

  &.CMS_Table_body-cell-shrink {
    @apply w-0;
  }
}

.CMS_Table_body-cell-content {
  @apply h-[44px]
    truncate
    w-full;
}

.CMS_Table_body-cell-link {
  @apply w-full
    h-full
    flex
    px-4
    py-3
    whitespace-nowrap;
}
